.profile {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.infoBlock {
    margin-bottom: 20px;
    display: flex;
    height: 300px;

}

@media screen and (max-width: 678px) {
    .infoBlock {
        flex-direction: column;
        height: auto;
        margin-bottom: 5px;
    }

}

.mainInfo {
    width: 28%;
    border-radius: 10px;
    padding: 40px;
    position: relative;
    display: flex;
    flex-direction: column;
    background: linear-gradient(to top, var(--wrap-bg-color) 73%, var(--color-block) 27%);

}

@media screen and (max-width: 678px) {
    .mainInfo {
        width: 100%;
        padding: 18px;
    }
}

